import React, { Component } from "react";
import "bootstrap/dist/css/bootstrap.css";
import "../App.css";
import Colors from "../color.js";
import { Z_FIXED } from "zlib";

const styleValores = {
  HeaderContainer: {
    backgroundColor: "white",
    height: "300vh",
    justifyContenr:'space-evenly',
    width: "100vw",
    display: "flex",
    alignItems: "center"
  },
  diferencialContainer: {
    height: "auto",
    backgroundColor: Colors.alternative,
    color: "white",
    padding: "10vw"
  }
};

class Valores extends Component {
  render() {
    return (
      <div
        id="ValoresContainer"
        className="ValoresContainer"
        style={styleValores.HeaderContainer}
      >
        <div className="progreso">
          <div id="avance" />
        </div>
        <div className="Valtexto">
          <h1 id="Valor">Flexibilidad</h1>
          <span id="DescripcionValor">
          Entendemos las necesidades de nuestros clientes 
            <br />
            y nos adaptamos a ellos.
          </span>
        </div>
        <br />
        <br />
        <br />
        <br />
        <div className="progreso ">
          <div id="avance" className="sixty" />
        </div>
        <div className="Valtexto">
          <h1 id="Valor">Atrevimiento</h1>
          <span id="DescripcionValor">
          Reinventamos la forma de ver el mundo 
            <br />
            rompiendo con los estereotipos convencionales.
          </span>
        </div>
        <br />
        <br />
        <br />
        <br />

        <div className="progreso ">
          <div id="avance" className="full" />
        </div>
        <div className="Valtexto">
          <h1 id="Valor">Audacia</h1>
          <span id="DescripcionValor">
            Somos determinados y no tenemos miedo,
            <br />
            lo cual es respaldado por nuestra ardua investigación con criterio y
            experiencia.
          </span>
        </div>
      </div>
    );
  }
}

export default Valores;
