import React, { Component } from "react";
import "bootstrap/dist/css/bootstrap.css";
import "../App.css";
import Colors from "../color.js";

const styleDiferencial = {
  HeaderContainer: {
    backgroundColor: "white",
    height: "100vh",
    width: "100vw",
    display:'flex',
    alignItems:'center'
  },
  diferencialContainer: {
      height:'auto',
    backgroundColor: Colors.alternative,
    color: 'white',
    padding: "10vw"
  }
};

class Diferencial extends Component {
  render() {
    return (
      <div style={styleDiferencial.HeaderContainer}>
        <div
          style={styleDiferencial.diferencialContainer}
          id="DiferencialContainer"
        >
          <h1 style={{fontWeight: 'bold'}}>Nuestro Diferencial</h1>
          <p>
            “Quemamos” estereotipos dentro del mercado, a través de una
            investigación profunda que nos permita encontrar nuevas
            oportunidades para generar impacto.
          </p>
        </div>
      </div>
    );
  }
}

export default Diferencial;
