import React, { Component } from "react";
import "bootstrap/dist/css/bootstrap.css";
import Colors from "../color.js";
import "../App.css";

const styleMetodologia = {
  HeaderContainer: {
    backgroundColor: "white",
    height: "100vh",
    width: "100vw"
  },
  Title: {
    padding: "1rem",
    fontSize: "4.5rem",
    fontWeight: "bold",
    color: Colors.alternative
  },
  Subtitle: {
    paddingTop: "1em",
    paddingBottom: "1em",
    fontStyle: "italic",
    display: "block",
    fontWeight: "bold"
  },
  text: {
    fontSize: "1.3em",
    display: "block",
    opacity: "0",
    textAlign:'justify',
    padding:'1em',
    transition: "opacity 1s"
  },
  box: {
    color: "white",
    flexGrow: 1,
    padding: "2vw"
  },
  boxContainer: {
    width: "100vw",
    display: "flex",
    alignItems: "stretch",
    justifyContent: "space-evenly",
    height: "calc(100vh - 6.5rem)"
  },
  
};

class Metodologia extends Component {
  render() {
    return (
      <div style={styleMetodologia.HeaderContainer}>
        <h1 style={styleMetodologia.Title}>Metodologia</h1>
        <div id="MetoBoxContainer" style={styleMetodologia.boxContainer}>
            <div style={styleMetodologia.box}>
                <div className="boxy" >
                <h3 style={styleMetodologia.Subtitle}>
                Definimos de lo convencional
                </h3>
                <p id="MetoText1" style={styleMetodologia.text}>
                Realizamos un análisis exhaustivo de la categoría, 
                las principales tendencias del mercado y personificamos los perfiles de consumidor
                </p>
                </div>
            </div>
            <br/>
            <br/>
            <br/>
            
            <div style={styleMetodologia.box}>
            <div className="boxy" >
            <h3 style={styleMetodologia.Subtitle}>
            Identificamos el valor diferencial
                </h3 >
                <p id="MetoText2" style={styleMetodologia.text}>
                Reconocemos la marca a través de la identificación de sus principales acciones e identificamos sus verdaderas necesidades y propuesta de valor única.</p>
                </div>
            </div>
            <br/>
            <br/>
            <br/>
            <div style={styleMetodologia.box}>
            <div className="boxy" >
            <h3 style={styleMetodologia.Subtitle}>
            Redefinimos nuestro mensaje
                </h3>
                <p id="MetoText3" style={styleMetodologia.text}>
                Deconstruimos  y reconceptualizamos los perfiles sociales a través del pensamiento paralelo para elaborar conceptos más impactantes, más auténticos.
                </p>
                </div>
            </div>
        </div>
      </div>
    );
  }
}

export default Metodologia;
