import React, { Component } from "react";
import "bootstrap/dist/css/bootstrap.css";
import "../App.css";
import Colors from "../color.js";
import Avatar from "@material-ui/core/Avatar";
import Kiara from "../assets/K1.jpg";
import Marco from "../assets/M1.jpg";
import Leslie from "../assets/L1.jpg";
import Diego from "../assets/D1.jpg";
import Alex from "../assets/A1.jpg";
const styleEquipo = {
  EquipoContainer: {
    backgroundColor: "white",
    height: "500vh",
    width: "100vw",
    alignItems:"center",
    flexDirection: "column",
    display: "flex",
    justifyContent: "center",
  },
  diferencialContainer: {
    height: "auto",
    backgroundColor: Colors.alternative,
    color: "white",
    padding: "10vw"
  },
  avatar: {
    height: "20rem",
    width: "20rem",
    maxWidth: "100vw"
  },
  Wrap: {
    marginLeft: "5vw",
    marginTop: "5vh",
    height:'100vh',
    alignItems: 'center',
    marginRight: "5vw",
    display: "flex"
  }
};

class Equipo extends Component {
  render() {
    return (
      <div style={styleEquipo.EquipoContainer}>
        <div className="avatar" style={styleEquipo.Wrap}>
          <div>
            <Avatar src={Kiara} style={styleEquipo.avatar} />
            <br />
            <h2>Kiara Burbano</h2>
          </div>
          <div className="textoEquipo">
            Sonrisas y alegrías 24/7, prefiere que le digan Kia porque cuando le
            dicen Kiara es que algo serio se asoma. Ella ha desarrollado su
            paciencia desde los 18 años debido a que su primer empleo fue ser
            docente de niños y también de adolescentes rebeldes ( sin causas).
            Cuando ella se traza una meta, no para hasta conseguirlo.
            Actualmente ama la cámara de su iphone y su cuenta en Instagram.
            Esto se debe a que está involucrada con la consciencia de
            alimentación sana, comparte los tips que le brinda su nutricionista
            y ama publicar fotos de sus deliciosas recetas. Adora escuchar a las
            personas, especialmente a sus futuros clientes. Fin de la
            conversación Escribe un mensaje...
          </div>
        </div>
        <div className="avatar" style={styleEquipo.Wrap}>
          <div>
            <Avatar src={Marco} style={styleEquipo.avatar} />
            <br />
            <h2>Marco Cardoso</h2>
          </div>
          <div className="textoEquipo">
            Soñador empedernido y curioso de profesión, Marco es un auténtico
            creyente de la igualdad y el bienestar social. Desde pequeño ha
            tenido harto a sus padres con la pregunta ¿y por qué? a todo; y
            ahora como publicista sabe que el cielo es el límite. Actualmente,
            se cataloga como orgulloso papá luchón de una pequeña bigotona de 4
            patas que ladra sin parar: Khala. El espíritu aventurero y la
            curiosidad de Marco son una herramienta para la investigación que
            apreciamos en Flama.
          </div>
        </div>
        <div className="avatar" style={styleEquipo.Wrap}>
          <div>
            <Avatar src={Diego} style={styleEquipo.avatar} />
            <br />
            <h2>Diego Ortega</h2>
          </div>
          <div className="textoEquipo">
            Soñador de día y de noche, sobre todo de noche en los brazos de
            Morfeo. Una persona que cree que en su vida pasada fue músico, y en
            esta nueva vida ejerce su pasada profesión haciendo Jazz en una muy
            simpática orquesta durante sus tiempos libres con mucha pasión y
            soltura. En algún momento de su actual vida quiso ser atleta, pero
            coyunturas de la vida hicieron que resulte bastante feliz y a gusto
            practicando bicicleta como aficionado. La publicidad es la
            herramienta que lo podrá ayudar a poder intentar ardua y
            constantemente a cumplir sus sueños en esta oportunidad que el
            universo le llama vida.
          </div>
        </div>
        <div className="avatar" style={styleEquipo.Wrap}>
          <div>
            <Avatar src={Alex} style={styleEquipo.avatar} />
            <br />
            <h2>Alexandra Agreda</h2>
          </div>
          <div className="textoEquipo">
            Daydreamer con capacidad de pisar tierra. una artista que disfruta
            representar sentimientos intensos. Apasionada por la fotografía, su
            red social favorita es Instagram. Disfruta del séptimo arte, incluso
            de esas películas catalogadas como tan malas que son buenas.
            Orgullosa amante del rosa pastel y unicornios. Compradora
            compulsiva; pobre, pero con gustos de rica. En algunos momentos es
            muy extrovertida y por otros disfruta demasiado de su aislamiento
            para continuar proyectos personales. Madre engreidora de un poodle
            llamado Alonso. Animal lover, falló 2 veces al intentar ser vegana
          </div>
        </div>
        <div className="avatar" style={styleEquipo.Wrap}>
          <div>
            <Avatar src={Leslie} style={styleEquipo.avatar} />
            <br />
            <h2>Leslie Caldas</h2>
          </div>
          <div className="textoEquipo">
            Estudió arte desde los 5 años en diferentes escuelas. Casi siempre
            la encuentras dibujando. Publicista en formación con alma de
            artista. Siempre tiene ideas para representar los temas que se le
            presenten y está atenta a lo que necesita el cliente. Es
            perfeccionista con todos los detalles de sus trabajos, siempre los
            revisa mientras escucha un poco de Michael Jackson. Sueña con ver
            las pinturas de sus artistas favoritos en los museos y no a través
            del Internet. Alérgica a varias cosas, pero no le importa
            experimentar otras. Le confía su creatividad a una taza de café, un
            poco de buena música y a la tranquilidad de la noche.
          </div>
        </div>
      </div>
    );
  }
}

export default Equipo;
